import './App.css';
import React from 'react';
import { useSharedLocalStorageEmitter } from '@hsl-fi/shared-local-storage';

const KEYS = ['saved-searches', 'favouriteStore', 'futureRoutes'];

const LocalStorageEmitter = () => {
  useSharedLocalStorageEmitter(KEYS, 'https://dev.hslfi.hsldev.com');

  return <div aria-hidden="true">LocalStorageEmitter</div>;
};


function App() {
  return (
    <div className="App">
        <LocalStorageEmitter/>
    </div>
  );
}

export default App;
